:root {
    font-size: 14px;
    width: 100vw;

}

.wide_grid{
    grid-column: span 2;
}

.grid_remarks{
    width:4rem;
    background: transparent;
    border: none;
    text-align: right;
    color: purple;
    font-weight: bold;
    height: 100%;
}

.grid_board_div {
    display: flex;
    justify-content: center;
}

.header_div {
    display: grid;
    justify-content: center;
}

.detail_board {
    display: grid;
    justify-content: center;
    grid-auto-flow: column;
    /* margin-top: 1rem; */
    padding: 1rem;
    /* margin-bottom: 1rem; */
    font-size: 1.1rem;
    grid-auto-columns: 3fr 1fr;

}

.date_input {}

.date_detail {

}

.water_mark{
    position:relative;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    text-align: right;
}

.year_gone, .same_year {
    background-color: rgb(204 255 223);
    border-radius: 0.5rem;
    opacity: 1.0 !important;
}
.month_gone, .same_month {
    background-color: rgb(254, 255, 204);
    border-radius: 0.5rem;
    opacity: 1.0 !important;
}
.day_gone, .same_day {
    background-color: rgb(255, 239, 204);
    border-radius: 0.5rem;
    opacity: 1.0 !important;
}
.time_gone, .same_time {
    background-color: rgb(255, 204, 204);
    border-radius: 0.5rem;
    opacity: 1.0 !important;
}

.char8_day {
    display: grid;
    padding-top: 0.5rem;
    padding-bottom: 0.8rem;
    padding-left: 0.2rem;
    padding-right: 0.2rem;
    border: 1px solid;
    align-items: center;

}

.button_row {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    margin-top: 1rem;
    /* grid-auto-columns: 1fr 1fr; */
    gap: 1rem;
}


.blue_grid {
    background-color: rgba(153, 204, 255, 0.7);
}

.yellow_grid {
    background-color: rgba(255, 255, 147, 0.7);
}

.grid_board {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    aspect-ratio: 1 / 1;
    font-size: 1.25rem
}

.grid {
    display: grid;
    min-height: 30%;
    padding: 1rem;
    grid-template-columns: repeat(3, 1fr) 1.5fr;
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    border: 1px solid lightgrey;
    text-wrap: nowrap;
    flex-wrap: nowrap;
}

.dis_prop {
    color: red;
}

.intro_prop {
    color: lightseagreen
}

.date_input {
    border: 1px solid;
    font-size: 1.25rem;
    margin-right: 1rem;
}

.date_input_row {
    display: flex;
    justify-content: flex-end;
    padding-right: 0.5rem;
    flex-direction: column;
}

.notes {
    display: grid;
    padding: 0.5rem;
    text-align: left;
    background-color: antiquewhite;
    max-width: 600px;
    font-size: 1.25rem;
}

.notes_div {
    display: grid;
    justify-content: center;
    margin: 1rem;
}

.message {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    flex-wrap: wrap;
}

.gakgok{
    display: grid;
}

.gakgok_title{
    font-weight: bold;
}

.gakgok_desc{

}

.wong_gong {
    font-weight: bolder;
}

.badge {

}

.good_gone {
    background-color: #73ffc98f;
}

.bad_gone {
    background-color: #ffb9b980;
}

.main_gone {
    background-color: mediumspringgreen;
    border-radius: 0.5rem;
}

.harm_main_gone{
    background-color: #fb6767;
    border-radius: 0.5rem;
}

.harm_notes{
    background-color: #fbc067;
    border-radius: 0.5rem;
}

.targets{
    background-color: #ffb32e;
    border-radius: 0.5rem;
}

.fullBtn{
    margin: 0.5rem;
}

.targets_div{
    margin: 0.5rem;
    font-size: 1.1rem;
}

.targets_span{
    padding: 0.1rem;
}

.targets_row{
    padding: 0.5rem;
}

.top_message_div{
    margin: 1rem;
}

.savedTime {
    display: flex;
    align-items: center;
}

.savedButtonRow{
    margin: 0.1rem;
    position: sticky;
    top: 0;
    background-color: white;
    align-items: center;
    z-index: 1000;
    height: 60px;
}

.savedTimeRow {
    border: lightgrey solid 1px;
    border-radius: 0.25rem;
    padding: 0.2rem;
    align-items: center;
}

.tabPage{
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 999;
    height: 40px;
}

.remarks_div{
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr 2fr;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    margin-left: 1rem;
    margin-right: 1rem;
    justify-content: center;
    align-items: center;
}